@import './_mixin.scss';
@import './_reset.scss';
@import './_svg.scss';
@import './_swiper.scss';
@import './_daterange.scss';

$default: #000;
$primary: #fff;
// ============================== Common 시작 ==============================

// 로고
i.logo {
	display: block;
	@include icon(logo);
}
// 폰트
h2,
h3,
h4,
.h2,
.h3,
.h4 {
	font-family: 'ChosunSm', serif;
}
h2,
.h2 {
	font-size: 3.2rem;
	@include mobile {
		font-size: 2.8rem;
	}
}
h3,
.h3 {
	font-size: 2.4rem;
}
h4,
.h4 {
	font-size: 2rem;
}
.text {
	word-break: break-all;
	text-align: justify;
	text-indent: 1rem;
	line-height: 130%;
	& + .text {
		margin-top: 0.2rem;
	}
}
b {
	font-family: 'ChosunKg', sans-serif;
}

// 스크롤바
body {
	@include scrollbar;
	overflow-y: scroll;
	overflow-x: hidden;
}
.modal-content {
	@include scrollbar(0.6rem, 0);
	overflow-y: scroll;
	overflow-x: hidden;
}
.table-wrap {
	@include scrollbar(0.6rem, 0);
	overflow-y: hidden;
	overflow-x: scroll;
}

// 피규어(이미지, 캡션)
figure {
	display: flex;
	flex-direction: column;
	gap: 0.4rem;
	img {
		object-fit: cover;
		aspect-ratio: 16/9;
	}
	figcaption {
		@include pseudo-icon(icon-up-fill);
		@include text-ellipsis(1);
		font-size: 1.4rem;
	}
	&.auto-height-figure {
		height: 100%;
		img {
			height: 0.1rem;
			flex-grow: 1;
		}
	}
	@include mobile {
		&.auto-height-figure {
			img {
				height: auto;
			}
		}
	}
}

// 높이조절 요소
.auto-height {
	display: flex;
	flex-direction: column;
	height: calc(100% - 5rem);
	gap: 2rem;
	&-component {
		height: 0.1rem;
		flex-grow: 1;
	}
	&.info {
		gap: 2.4rem;
		height: 100%;
	}
}

// 디자인된 보더
.designed-border {
	position: relative;
	display: block;
	min-height: 6rem;
	.border-top,
	.border-bottom {
		&::before,
		&::after {
			content: '';
			position: absolute;
			@include icon(border-coner, 2.1rem);
		}
	}
	.border-top::before {
		left: 0;
		top: 0;
	}
	.border-top::after {
		right: 0;
		top: 0;
		transform: rotate(90deg);
	}
	.border-bottom::after {
		right: 0;
		bottom: 0;
		transform: rotate(180deg);
	}
	.border-bottom::before {
		left: 0;
		bottom: 0;
		transform: rotate(270deg);
	}
	.border-inner {
		transition-duration: 0.5s;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 100%;
		clip-path: polygon(
			2.1rem 0%,
			calc(100% - 2.1rem) 0%,
			calc(100% - 2.1rem) 2.1rem,
			100% 2.1rem,
			100% calc(100% - 2.1rem),
			calc(100% - 2.1rem) calc(100% - 2.1rem),
			calc(100% - 2.1rem) 100%,
			2.1rem 100%,
			2.1rem calc(100% - 2.1rem),
			0% calc(100% - 2.1rem),
			0% 2.1rem,
			2.1rem 2.1rem
		);
		border: 0.6rem double $default;
	}
}

// 메뉴버튼
.btn-menu {
	position: relative;
	width: 3.6rem;
	height: 3.6rem;
	span {
		display: block;
		width: 100%;
		height: 0.2rem;
		position: absolute;
		left: 0;
		top: calc(50% - 0.1rem);
		background: $default;
		transition-duration: 0.5s;
	}
	&::before,
	&::after {
		content: '';
		display: block;
		width: 50%;
		height: 0.2rem;
		position: absolute;
		background: $default;
		transition-duration: 0.5s;
	}
	&::before {
		left: 0;
		top: calc(25% - 0.1rem);
	}
	&::after {
		right: 0;
		bottom: calc(25% - 0.1rem);
	}
	&.active {
		span {
			transform: rotate(-45deg);
		}
		&::before {
			transform: rotate(45deg);
			left: 0.3rem;
			top: calc(25% + 0.2rem);
		}
		&::after {
			transform: rotate(45deg);
			right: 0.3rem;
			bottom: calc(25% + 0.2rem);
		}
	}
}

.loading {
	position: fixed;
	width: 100vw;
	height: 100vh;
	z-index: 999;
	background-color: #cbcbcb;
	.spinner {
		position: absolute;
		left: calc(50% - 5.5rem);
		top: calc(50% - 5.5rem);
		width: 11rem;
		height: 11rem;
		display: flex;
		justify-content: center;
		align-items: center;
		color: rgba($default, 0.5);
		animation: fadeinout 3s linear infinite;
		letter-spacing: -0.02rem;
		font-family: 'Helvetica', 'Arial', 'Verdana', sans-serif;
		&::before,
		&::after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			border: 0.2rem solid;
			border-radius: 50%;
		}
		&::before {
			border-color: transparent rgba($default, 0.5);
			width: 100%;
			height: 100%;
			animation: rotation 3s linear infinite reverse;
		}
		&::after {
			content: '';
			border-color: rgba($default, 0.5) transparent;
			width: 120%;
			height: 120%;
			left: -10%;
			top: -10%;
			animation: rotation 3s linear infinite;
		}
	}
	// &.active {
	// animation: fade-out 1s linear 3.5s 1 forwards;
	// }

	& + #wrap {
		height: 0;
		overflow: hidden;
	}

