.checkbox {
	position: relative;
	input[type='checkbox'] {
		position: absolute;
		top: 0;
		left: 0;
		border: 0;
		width: 100%;
		height: 100%;
		margin: 0;
		cursor: pointer;
		& + label {
			position: relative;
			&::before {
				content: '';
				display: block;
				width: 2.4rem;
				height: 2.4rem;
				@include icon(icon-checkbox, 2.4rem);
			}
		}
		&:checked + label::before {
			@include icon(icon-checkbox-checked, 2.4rem, 'fff');
		}
		&:disabled,
		&:disabled:checked {
			& + label {
				&::before {
					opacity: 0.3;
				}
			}
		}
	}
}
